  components: {
    codemirror
  },
  data(){
    return{
      tabledict:'',
      search:'',
      input:'',
      word1:'',
      word2:'',
      word3:'',
      word4:'',
      time1:'',
      time2:'',
      time3:'',
      time4:'',
      dict:'',
      drawer1: false,
      drawer2: false,
      drawer3: false,
      drawer4: false,
