    cutWord(){
      this.cutWord1()
      this.cutWord2()
      this.cutWord3()
      this.cutWord4()
    },
    cutWord1(){
      var timeStart = Date.now()//计时器,计算代码运行效率
      this.word1 = ''           //初始化分词结果
      //词典:this.dict;句子:sentence;结果:result;长度(max_length)取为4;
      var result = new Array()  //定义数组存储分词结果
      var sentence = this.input //把输入框内容赋值给sentence
      var len = sentence.length //句子长度:len

      while(len > 0){                   //循环终止条件:句子长度为0
        var tmp = sentence.slice(0,4)   //截取4个长度的词
        while(!this.dict.includes(tmp)){//循环终止条件:词典中包含该词
          if(tmp.length == 1){          //如果截到只剩一个词则跳出
            break
          }
          tmp = tmp.slice(0,tmp.length-1)//不断缩短词
        }
        result.push(tmp)//将得到的词压入数组
        sentence = sentence.slice(tmp.length,) //已经分出的词删掉
        len = sentence.length//句子长度重新赋值
      }
      for(var i=0;i<result.length;i++){
        if(this.word1 == ''){
          this.word1 = result[i] 
        }
        else{
          this.word1 = this.word1+"/"+result[i] //构造一个字符串,用/隔开
        }

      }
      var timeEnd = Date.now() //终止计时
      var timeDiff =  timeEnd - timeStart 
      this.time1 = timeDiff+'ms'
    },
    cutWord2(){
      var timeStart = Date.now()//计时器,计算代码运行效率
