        var tmp = sentence.slice(0,tmp_len) //从第一个字开始,向后截取
        while(!this.dict.includes(tmp)){    //循环跳出条件:词典包含该词语
          if(tmp_len == 4 || len == 1){     //循环跳出条件:取够了4个字,或句子长度为1
            tmp_len = 1
            break
          }
          tmp_len+=1
          tmp = sentence.slice(0,tmp_len)   //向后截取句子
        }
        result.push(sentence.slice(0,tmp_len))
        sentence = sentence.slice(tmp_len,) //向后截取句子
        len = sentence.length
      }
      for(var i=0;i<result.length;i++){
        if(this.word2 == ''){
          this.word2 = result[i] 
        }
        else{
          this.word2 = this.word2+"/"+result[i] //构造一个字符串,用/隔开
        }

      }
      var timeEnd = Date.now() //终止计时
      var timeDiff =  timeEnd - timeStart 
      this.time2 = timeDiff+'ms'
    },
    cutWord3(){
      var timeStart = Date.now()//计时器,计算代码运行效率
      this.word3 = ''           //初始化分词结果
      //词典:this.dict;句子:sentence;结果:result;长度(max_length)取为4;
