        //console.log(tmp)
        while(!this.dict.includes(tmp)){//循环终止条件:词典中包含该词
          if(tmp.length == 1){          //如果截到只剩一个字则跳出
            break
          }
          tmp = tmp.slice(1,)//不断缩短词
        }
        result.push(tmp)//将得到的词压入数组
        sentence = sentence.slice(0,sentence.length-tmp.length) //已经分出的词删掉
        len = sentence.length//句子长度重新赋值
      }
      for(var i=0;i<result.length;i++){
        if(this.word3 == ''){
          this.word3 = result[result.length-i-1] 
        }
        else{
          this.word3 = this.word3+"/"+result[result.length-i-1] //构造一个字符串,用/隔开
        }

      }
      console.log('word3',this.word3)
      var timeEnd = Date.now() //终止计时
      var timeDiff =  timeEnd - timeStart 
      this.time3 = timeDiff+'ms'
      //this.input = ''          //初始化输入框
    },
    cutWord4(){
