      var sentence = this.input //把输入框内容赋值给sentence
      var result = new Array()  //定义数组存储分词结果
      var len = 1               //定义初始长度,首先只取1位
      var start = sentence.length-len //定义初始位置
      while(start >= 0){        //跳出循环:初始位置退到第一个字
        while(!this.dict.includes(sentence.slice(start,start+len))){ //跳出循环:截取的词在词典中
          len += 1
          start -= 1            //位置移动
          if(len > 4 || start < 0 ){ //判断是否截取超过4个,或已退到第一个字
            start += len - 1
            len = 1
            break
          }
        }
        result.push(sentence.slice(start,start+len)) 
        start -= 1              //退一位
        len = 1
      }
      for(var i=0;i<result.length;i++){
        if(this.word4 == ''){
          this.word4 = result[result.length-i-1]  
        }
        else{
          this.word4 = this.word4+"/"+result[result.length-i-1] //构造一个字符串,用/隔开
        }

      }
      var timeEnd = Date.now() //终止计时
      var timeDiff =  timeEnd - timeStart 
      this.time4 = timeDiff+'ms'
    },
    getword(){
      this.$axios.get('static/cutword.json').then(res => {   
        var data = res['data']
        var result = new Array()
        var jsontable = new Array()
        for(var i=0;i<data.length;i++){
          if(data[i]['data'].length>1){
          //console.log(data[i]['data'],data[i]['data'].length)
          var tmp = {'word':data[i]['data']}
          jsontable.push(tmp)
          result.push(data[i]['data'])
          }
        }
        this.dict = result
        this.tabledict = jsontable
        console.log(jsontable)
      })
  },
