      sentence = sentence.slice(tmp.length,) //已经分出的词删掉\n\
      len = sentence.length//句子长度重新赋值\n\
      }\n\
      for(var i=0;i<result.length;i++){\n\
      if(this.word1 == ''){\n\
      this.word1 = result[i]\n\
      }\n\
      else{\n\
      this.word1 = this.word1+'/'+result[i] //构造一个字符串,用/隔开\n\
      }\n\
      var timeEnd = Date.now() //终止计时\n\
      var timeDiff =  timeEnd - timeStart \n\
      this.time1 = timeDiff+'ms'\n\
      "
      this.code3 = "      var timeStart = Date.now()//计时器,计算代码运行效率\n\
      this.word3 = ''           //初始化分词结果\n\
      //词典:this.dict;句子:sentence;结果:result;长度(max_length)取为4;\n\
      var result = new Array()  //定义数组存储分词结果\n\
      var sentence = this.input //把输入框内容赋值给sentence\n\
      var len = sentence.length //句子长度:len\n\
      while(len > 0){                   //循环终止条件:句子长度为0\n\
      var tmp = sentence.slice(len-4,)   //截取4个长度的词\n\
      while(!this.dict.includes(tmp)){//循环终止条件:词典中包含该词\n\
        if(tmp.length == 1){          //如果截到只剩一个字则跳出\n\
          break\n\
        }\n\
        tmp = tmp.slice(1,)//不断缩短词\n\
      }\n\
      result.push(tmp)//将得到的词压入数组\n\
      sentence = sentence.slice(0,sentence.length-tmp.length) //已经分出的词删掉\n\
      len = sentence.length//句子长度重新赋值\n\
      }\n\
      for(var i=0;i<result.length;i++){\n\
      if(this.word3 == ''){\n\
      this.word3 = result[result.length-i-1]\n\
      }\n\
      else{\n\
      this.word3 = this.word3+'/'+result[result.length-i-1] //构造一个字符串,用/隔开\n\
      }\n\
      var timeEnd = Date.now() //终止计时\n\
      var timeDiff =  timeEnd - timeStart \n\
      this.time3 = timeDiff+'ms'\n\
      "
      this.code2="      var timeStart = Date.now()//计时器,计算代码运行效率\n\
      this.word2 = ''           //初始化分词结果\n\
      //词典:this.dict;句子:sentence;结果:result;长度(max_length)取为4;\n\
      var result = new Array()  //定义数组存储分词结果\n\
      var sentence = this.input //把输入框内容赋值给sentence\n\
      var len = sentence.length //句子长度:len\n\
      while(len > 0){                       //跳出循环条件:句子长度为0\n\
        var tmp_len = 1\n\
        var tmp = sentence.slice(0,tmp_len) //从第一个字开始,向后截取\n\
        while(!this.dict.includes(tmp)){    //循环跳出条件:词典包含该词语\n\
          if(tmp_len == 4 || len == 1){     //循环跳出条件:取够了4个字,或句子长度为1\n\
            tmp_len = 1\n\
            break\n\
          }\n\
          tmp_len+=1\n\
          tmp = sentence.slice(0,tmp_len)   //向后截取句子\n\
        }\n\
        result.push(sentence.slice(0,tmp_len))\n\
        sentence = sentence.slice(tmp_len,) //向后截取句子\n\
        len = sentence.length\n\
      }\n\
      for(var i=0;i<result.length;i++){\n\
        if(this.word2 == ''){\n\
          this.word2 = result[i] \n\
        }\n\
        else{\n\
          this.word2 = this.word2+'/'+result[i] //构造一个字符串,用/隔开\n\
        }\n\
      }\n\
      var timeEnd = Date.now() //终止计时\n\
      var timeDiff =  timeEnd - timeStart\n\
      this.time2 = timeDiff+'ms'\n\
      ",
      this.code4 = "\
      var timeStart = Date.now()//计时器,计算代码运行效率 \n\
      this.word4 = ''\n\
      var sentence = this.input //把输入框内容赋值给sentence\n\
      var result = new Array()  //定义数组存储分词结果\n\
      var len = 1               //定义初始长度,首先只取1位\n\
      var start = sentence.length-len //定义初始位置\n\
      while(start >= 0){        //跳出循环:初始位置退到第一个字\n\
        while(!this.dict.includes(sentence.slice(start,start+len))){\n\
          //跳出循环:截取的词在词典中\n\
          len += 1\n\
          start -= 1            //位置移动\n\
          if(len > 4 || start < 0 ){ //判断是否截取超过4个,或已退到第一个字\n\
            start += len - 1\n\
            len = 1\n\
            break\n\
          }\n\
        }\n\
        result.push(sentence.slice(start,start+len)) \n\
        start -= 1              //退一位\n\
        len = 1\n\
      }\n\
      for(var i=0;i<result.length;i++){\n\
        if(this.word4 == ''){\n\
          this.word4 = result[result.length-i-1]\n\
        }\n\
        else{\n\
          this.word4 = this.word4+'/'+result[result.length-i-1] //构造一个字符串,用/隔开\n\
        }\n\
      }\n\
      var timeEnd = Date.now() //终止计时\n\
      var timeDiff =  timeEnd - timeStart\n\
      this.time4 = timeDiff+'ms'\
      "
    }
  },
  mounted() {
    this.getword()
    this.getcode()
  },
}
